@charset 'utf-8';
html, body {
    font-weight: 200;
    font: 16px/1.5 "Helvetica Neue",Helvetica,Arial,"Microsoft Yahei","Hiragino Sans GB","Heiti SC","WenQuanYi Micro Hei",sans-serif;
    color: #333;
    font-size: 14px
}
html, body, body>div:first-child {
    height: 100%
}
.main-wrapper {
    height: auto;
    min-height: 100%;
    width: 100%;
    overflow-x: hidden;
    background: #F2F2F2
}
.main-wrapper .main {
    -webkit-box-sizing: border-box;
               -moz-box-sizing: border-box;
            box-sizing: border-box;
    /*width: 1200px;*/
    margin: 0 auto;
    padding-bottom: 80px
}
.footer-wrapper {
    margin-top: -80px;
    height: 80px;
    clear: both;
    background: #f1f1f1
}
.fl {
    float: left
}
.fr {
    float: right
}
.clearfix {
    display: block
}
.clearfix::after {
    display: block;
    visibility: hidden;
    clear: both;
    height: 0;
    line-height: 0;
    font-size: 0;
    content: '.'
}
button {
    outline: 0
}
.button {
    display: inline-block;
    position: relative;
    width: auto;
    -webkit-appearance: none;
    font-family: inherit;
    font-size: inherit;
    border: 0
}
.button:focus {
    outline: none
}
.button:after {
    position: absolute;
    width: 200%;
    height: 200%;
    -webkit-box-sizing: border-box;
               -moz-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-border-radius: 10px;
            border-radius: 10px;
    top: 0;
    left: 0;
    border: 1px solid rgba(7, 17, 27, 0.1);
    -webkit-transform: scale(0.5);
    -ms-transform: scale(0.5);
         -moz-transform: scale(0.5);
         -o-transform: scale(0.5);
        transform: scale(0.5);
    -webkit-transform-origin: 0 0;
    -ms-transform-origin: 0 0;
         -moz-transform-origin: 0 0;
         -o-transform-origin: 0 0;
        transform-origin: 0 0;
    content: ' '
}
.button[disabled] {
    cursor: not-allowed
}
.button.button-block {
    display: block;
    width: 100%;
    padding: 12px 0
}
.button.button-blue {
    background-color: rgb(76, 147, 253);
    color: #fff
}
.button.button-default {
    background-color: #f9f9f9;
    padding: 12px;
    color: #333;
    -webkit-border-radius: 0;
            border-radius: 0
}
.button.button-default.active {
    color: #fff;
    background-color: #4c93fd
}
.button.button-default[disabled] {
    background-color: #f6f6f6;
    color: rgba(7, 17, 27, 0.2)
}
.button.button-red {
    background-color: #E64340;
    color: #fff
}
.button.button-red:active {
    background-color: #CE3C39
}
.button.button-blue:active {
    opacity: 0.7
}
.button.button-blue[disabled] {
    background-color: rgba(7, 17, 27, 0.2);
    color: rgba(255, 255, 255, 0.7)
}
.button.button-red[disabled] {
    background-color: #EC8B89;
    color: #fff
}
/*1px button*/
.button.button-plain {
    background: #fff;
    -webkit-border-radius: 0;
            border-radius: 0;
    font-size: 20px;
    padding: 8px 0
}
.button.button-plain:after {
    display: none
}
.button.button-plain.button-plain-blue {
    color: #4c93fd;
    border: 1px solid #4c93fd
}
.button.button-plain.button-plain-orange {
    color: #ff9c31;
    border: 1px solid #ff9c31
}
.button.button-plain.button-plain-pink {
    color: #ff7478;
    border: 1px solid #ff7478
}
a {
    color: #999;
    text-decoration: none;
    cursor: pointer
}
a:hover {
    text-decoration: none;
    color: #3385ff
}
a:visited {
    color: #999
}
a:visited:hover {
    color: #3385ff
}
