    border: 0;
    vertical-align: middle
}
table {
    border-collapse: collapse;
    border-spacing: 0
}
p {
    word-wrap: break-word
}
a, input, button, select {
    outline: 0
}
i {
    font-style: normal
}
button, a {
    cursor: pointer
}



