
.popper {
  width: auto;
  background-color: #fff;
  text-align: center;
  padding: 10px;
  display: inline-block;
  -webkit-border-radius: 3px;
          border-radius: 3px;
  position: absolute;
  border: 1px #eaeaea solid;
  z-index: 200000;
}
.popper[x-placement^="top"] {
  margin-bottom: 5px;
}
.popper[x-placement^="bottom"] {
  margin-top: 5px;
}
.popper[x-placement^="right"] {
  margin-left: 5px;
}
.popper[x-placement^="left"] {
  margin-right: 5px;
}





