
.ts-image--popup {
  position: fixed;
  z-index: 99999;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
             -moz-transform: translate(-50%, -50%);
               -o-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  left: 50%;
  -webkit-animation: imgfade .4s;
             -moz-animation: imgfade .4s;
               -o-animation: imgfade .4s;
          animation: imgfade .4s;
  max-height: 70%;
  max-width: 70%
}
.ts-image--popup:after {
  content: '&times';
  font-size: 12px
}



