:root {
    /*checkbox*/
    /*nav*/
    /*Image*/
    /*Radio*/
    /*Filter*/
    /*menuTable*/
    /*Dialog*/
    /*Input*/
    /*select*/
    /*button*/
    /*Grid*/
    /*navbar*/
}
.ts-navbar {
    background-color: #fff;
    text-align: center;
    border-bottom: 1px solid #eaeaea;
}
.ts-navbar-wrap {
    overflow: hidden;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: -moz-box;
    display: flex;
    margin-bottom: -1px;
}
.ts-navbar-wrap .ts-tab-item {}
.ts-navbar-wrap .ts-tab-item:last-child {
    border-right: 0;
}
.ts-navbar-wrap .ts-tab-item.is-selected {
    border-bottom: 3px solid #4C93FD;
    color: #4C93FD;
}
.ts-navbar-wrap.is-bordered .ts-tab-item {
    border: 1px solid transparent
}
.ts-navbar-wrap.is-bordered .ts-tab-item.is-selected {
    border: 1px solid #eaeaea;
    border-bottom-color: #fff;
    -webkit-border-radius: 2px 2px 0 0;
            border-radius: 2px 2px 0 0;
}
.ts-navbar-wrap.is-bordered.ts-navbar-wrap {
    padding-left: 40px;
    padding-right: 40px;
}



